<template>
  <v-app id="main">
    <page-bar />
    <page-view />
    <default-nav />
  </v-app>
</template>

<script>
import PageView from './View.vue'
import DefaultNav from '../default/Nav.vue'
import PageBar from './AppBar.vue'

export default {
  name: 'PageLayout',
  components:{
    DefaultNav,
    PageBar,
    PageView,
  },
  data:()=>({
    offsetTop: 0,
    scrollTop: false,
  }),
  methods: {
    onScroll(e){
      this.offsetTop = e.target.scrollTop
      if (e.target.scrollTop > 0){
        scrollTop = true
      }
    },
  }
}

</script>

